class Emerga {
  /**
   * 
   */
  constructor() {
    // this.$ = require( "jquery" );
    this.d3 = require( "d3" );
    this.dat = require('dat-gui');
    this.Stats = require( "stats.js" );
    this.cytoscape = require( "cytoscape" );
    
    this.stats = [];
    this.guis = {};
  }
  /**
   * 
   */
  initCanvas(canvasId) {
    var canvas = document.getElementById(canvasId);
    if(canvas) {
      this.canvas = canvas;
      this.context = this.canvas.getContext("2d");
    }
    return this;    
  }
  /**
   * 
   * @param {*} type 
   */
  initStats(type) {
    if(this.stats[type]) {
      return;
    }
    this.stats[type] = new this.Stats();
    this.stats[type].showPanel(type); // 0: fps, 1: ms, 2: mb, 3+: custom
    document.body.appendChild(this.stats[type].dom);
    return this;
  }
  /**
   * 
   * @param {*} type 
   */
  initGUI(type, etc) {
    if(this.guis[type]) {
      return;
    }
    this.guis[type] = new this.dat.GUI(etc);
    return this;
  }
  /**
   * 
   * @param {*} script 
   */
  run(script) {
    if (!script) {
      return;
    }
    var instance = new script(this);
    if(instance.init) {
      instance.init(this);
    }
    if(instance.run) {
      instance.run(this);
    }
    return this;
  }
}
module.exports = new Emerga();
